* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}
.NavBar {
    background: #0a6259;
    height: 75px;
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index:999;
   
    
}

.navbar-brand{
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
    
}

.justify-content-end {
    justify-content: end;
    color: black;
}


.navbar-brandlogo {
    inline-size: auto;
}

#navbar-brandname, #navbar-brandname:hover, #navbar-brandname:active #navbar-brandname:visited {
    text-decoration: none;
    font-family: 'Pacifico', cursive;
    color: #fece63;
    font-size: 2.5rem;
    text-shadow: -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff, 1px 1px 0 #fff;
    }

.button {
display:inline-block;
padding:0.3em 1.2em;
margin:0 0.1em 0.1em 0;
border:0.16em solid rgba(255,255,255,0);
border-radius:2em;
box-sizing: border-box;
text-decoration:none;
font-family:'Roboto',sans-serif;
font-weight:300;
color:#ffffff;
background-color: #fece63;
text-shadow: 0 0.04em 0.04em rgba(0,0,0,0.35);
text-align:center;
transition: all 0.2s;
}

a.button:hover{
border-color: rgb(248, 154, 30);
}

#dropdown {
    color: black;
    background-color: #0a6259;
}

