.footer-links-pages {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content:space-evenly;
}

.footer-link-wrapper {
  display: flex;
}

.footer-link-items a {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0px;
  width: 160px;
  font-size: 20px;
  color: rgb(44, 43, 43)
}

.footer-link-items h2 {
  margin-bottom: 16px;
  flex-direction: row;
}

.footer-link-items > h2 {
  color: #0a6259;
  font-weight: bold;
}

.footer-link-items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
  color: #e9e9e9;
  transition: 0.3s ease-out;
}

.footer-email-form h2 {
  margin-bottom: 2rem;
}

.footer-input::-webkit-input-placeholder {
  color: #b1b1b1;
}

.footer-input:-ms-input-placeholder {
  color: #b1b1b1;
}

.footer-input::placeholder {
  color: #b1b1b1;
}



@media screen and (max-width: 820px) {
  .footer-links {
    padding-top: 2rem;
  }

  .footer-input {
    width: 100%;
  }

  .footer-link-wrapper {
    flex-direction: column;
  }

  
}

